import React from 'react';
import { withTranslation } from 'react-i18next';
import Panel from '../../../components/Panel/Panel';
import Input from '../../../components/Ui/Input/Input';
import { t } from '../../../i18n';
import { CONSUMER_LOAN_NAMESPACE } from '../constants';

class ConsoSimulator extends React.PureComponent {
  constructor() {
    super();
    this.state = {
      creditAmount: 20000,
      enableAmountSlider: true,
      rate: 4.5,
      duration: 6,
      monthlyPayment: 1200,
      tva: false,
      tvaRate: 0,
    };
    this.tvaRate = 0;
  }
  _onChangeAmountSlider = e => {
    if (this.state.enableAmountSlider) {
      let newCreditAmount;
      if (e.target && e.target.nodeName === 'INPUT') {
        newCreditAmount = parseInt(e.target.value);
      } else {
        newCreditAmount = e.value;
      }
      this.setState(
        { creditAmount: newCreditAmount },
        this._simulateMonthlyPayment,
      );
    }
  };
  _onChangeRateSlider = e => {
    let newRate;
    if (e.target && e.target.nodeName === 'INPUT') {
      newRate = e.target.value;
    } else {
      newRate = e.value;
    }
    this.setState(
      { rate: newRate },
      this.state.enableAmountSlider
        ? this._simulateAmount
        : this._simulateMonthlyPayment,
    );
  };
  _onChangeDurationSlider = e => {
    let newDuration;
    if (e.target && e.target.nodeName === 'INPUT') {
      newDuration = parseInt(e.target.value);
    } else {
      newDuration = e.value;
    }
    this.setState(
      { duration: newDuration },
      this.state.enableAmountSlider
        ? this._simulateAmount
        : this._simulateMonthlyPayment,
    );
  };
  _onChangeMonthlyPaymentSlider = e => {
    if (!this.state.enableAmountSlider) {
      let newMonthlyPayment;
      if (e.target && e.target.nodeName === 'INPUT') {
        newMonthlyPayment = parseInt(e.target.value);
      } else {
        newMonthlyPayment = e.value;
      }
      this.setState(
        { monthlyPayment: newMonthlyPayment },
        this._simulateAmount,
      );
    }
  };
  _toggleTVA = e => {
    let enabledTVA;
    if (e.target && e.target.nodeName === 'INPUT') {
      enabledTVA = e.target.value;
    } else {
      enabledTVA = e.value;
    }
    this.setState(
      { tva: enabledTVA },
      this.state.enableAmountSlider
        ? this._simulateAmount
        : this._simulateMonthlyPayment,
    );
  };
  _simulateAmount = () => {
    const simulationValues = {
      tva: this.state.tva,
      rate: this.state.rate,
      duration: this.state.duration,
      monthlyPayment: this.state.monthlyPayment,
    };
    const tvaValue = simulationValues.tva ? this.tvaRate / 100 : 0;
    const x = (1 + tvaValue) * (simulationValues.rate / 1200);
    const y = Math.pow(1 + x, simulationValues.duration);
    const z = (1 - 1 / y) / x;
    const creditAmount = simulationValues.monthlyPayment * z;
    this.setState({ creditAmount: creditAmount });
  };
  _simulateMonthlyPayment = () => {
    const simulationValues = {
      creditAmount: this.state.creditAmount,
      tva: this.state.tva,
      rate: this.state.rate,
      duration: this.state.duration,
    };
    const tvaValue = simulationValues.tva ? this.tvaRate / 100 : 0;
    const x = (1 + tvaValue) * (simulationValues.rate / 1200);
    const y = Math.pow(1 + x, simulationValues.duration);
    const z = x / (1 - 1 / y);
    const monthlyPayment = simulationValues.creditAmount * z;
    this.setState({ monthlyPayment: monthlyPayment });
  };
  _setTVA = tvaRatePercentage => {
    const tvaRate = tvaRatePercentage
      .split('')
      .filter(el => Number.isInteger(Number.parseInt(el)) || el === '.')
      .join('')
      .replace('.', 'PLACEHOLDER')
      .replace(/\./g, '')
      .replace('PLACEHOLDER', '.');
    if (tvaRate <= 100 && tvaRate >= 0)
      this.setState(
        {
          tvaRate: tvaRate,
        },
        this.state.tva && this.state.enableAmountSlider
          ? this._simulateAmount
          : this._simulateMonthlyPayment,
      );
  };
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevState.tvaRate !== this.state.tvaRate) {
      this.tvaRate =
        this.state.tvaRate === ''
          ? 0
          : parseFloat(this.state.tvaRate).toFixed(2);
      this.forceUpdate();
    }
  }
  render() {
    const tvaRate = this.tvaRate;
    return (
      <div className="col-sm-12">
        <Panel
          title={t(`${CONSUMER_LOAN_NAMESPACE}simulateYourConsumerCredit`)}
        >
          <div className="simulator_wrapper design-two">
            <form>
              <Input
                unit={t(`${CONSUMER_LOAN_NAMESPACE}currency`)}
                inputField={t(`${CONSUMER_LOAN_NAMESPACE}creditAmount`)}
                elementType="SimulatorSlider"
                minValue={2000}
                maxValue={300000}
                value={parseInt(this.state.creditAmount)}
                onchange={this._onChangeAmountSlider}
                lock
                checked={this.state.enableAmountSlider}
                onLockChange={e =>
                  this.setState({
                    enableAmountSlider: e.target.checked,
                  })
                }
              />
              <Input
                floatNumber
                step={0.1}
                unit="%"
                inputField={t(`${CONSUMER_LOAN_NAMESPACE}rate`)}
                elementType="SimulatorSlider"
                minValue={1}
                maxValue={18}
                value={this.state.rate}
                onchange={this._onChangeRateSlider}
              />
              <Input
                unit={t(`${CONSUMER_LOAN_NAMESPACE}months`)}
                inputField={t(`${CONSUMER_LOAN_NAMESPACE}duration`)}
                elementType="SimulatorSlider"
                minValue={6}
                maxValue={84}
                value={this.state.duration}
                onchange={this._onChangeDurationSlider}
              />
              <Input
                unit={t(`${CONSUMER_LOAN_NAMESPACE}currencyByMonth`)}
                inputField={t(`${CONSUMER_LOAN_NAMESPACE}monthly`)}
                elementType="SimulatorSlider"
                minValue={1}
                maxValue={30000}
                value={parseInt(this.state.monthlyPayment)}
                onchange={this._onChangeMonthlyPaymentSlider}
                lock
                checked={!this.state.enableAmountSlider}
                onLockChange={e =>
                  this.setState({
                    enableAmountSlider: !e.target.checked,
                  })
                }
              />
              <div className="tvaRate">
                <label htmlFor="tvaRate">
                  {t(`${CONSUMER_LOAN_NAMESPACE}tvaRate`)}
                </label>
                <input
                  name="tvaRate"
                  onChange={e => {
                    this._setTVA(e.target.value);
                  }}
                  value={this.state.tvaRate}
                  className="form-control"
                  placeholder={t(`${CONSUMER_LOAN_NAMESPACE}tvaRate`)}
                />
              </div>
              <Input
                label={t(`${CONSUMER_LOAN_NAMESPACE}withTva`, { tvaRate })}
                elementType="SwitchTvaSimulator"
                checked={this.state.tva}
                onchange={this._toggleTVA}
              />
            </form>
          </div>
        </Panel>
      </div>
    );
  }
}

export default withTranslation()(ConsoSimulator);
