import React from 'react';
import { Link } from 'react-router-dom';
import { withTranslation } from 'react-i18next';
import HeadNav from '../../../components/PageHeadNav/PageHeadNav';
import PageHeader from '../../../components/PageHeader/PageHeader';
import { t } from '../../../i18n';
import { SIMULATOR_NAMESPACE, TABS } from '../constants';
import ConsoSimulator from './ConsoSimulator';
import MortgageSimulator from './MortgageSimulator';

const Feature = props => {
  const _getTabTitle = () => {
    const tab = TABS.filter(tab => tab.id === props.activeTab)[0];
    return tab.title;
  };
  const _renderActiveTab = () => {
    switch (props.activeTab) {
      case TABS[0].id:
        return <ConsoSimulator />;
      case TABS[1].id:
        return <MortgageSimulator />;
      default:
        return null;
    }
  };
  return (
    <div className="page-content">
      <div className="page-content_side">
        <PageHeader text={t(`${SIMULATOR_NAMESPACE}title`)}>
          <li key="1">
            <Link to="/dashboard">
              {t(`${SIMULATOR_NAMESPACE}breadCrumbFirstLink`)}
            </Link>
          </li>
          <li key="2">
            <Link to="/simulator">
              {t(`${SIMULATOR_NAMESPACE}breadCrumbSecondLink`)}
            </Link>
          </li>
          <li className="active" key="3">
            {t(_getTabTitle())}
          </li>
        </PageHeader>
        <div className="page-content_inner">
          <HeadNav>
            {TABS.map(tab => (
              <li key={tab.id}>
                <Link
                  className={tab.id === props.activeTab && 'active'}
                  title={t(tab.title)}
                  to="#"
                  onClick={() => props.activateTab(tab.id)}
                >
                  {t(tab.title)}
                </Link>
              </li>
            ))}
          </HeadNav>
          <div className="main-content container-fluid">
            {_renderActiveTab()}
          </div>
        </div>
      </div>
    </div>
  );
};

export default withTranslation()(Feature);
